/**
 * Session Cam module
 * @see https://help.sessioncam.com
 * @see https://help.sessioncam.com/hc/en-gb/categories/200081186-Setup-Guide
 * @see https://help.sessioncam.com/hc/en-gb/sections/206623567-6-Advanced-Custom-Code
 */

import loadjs from 'loadjs';
import { trim } from '../utils/shims';

export function isSessionCamEnabled() {
  return !!window.sessionCamRecorder;
}

/**
 * Gets data by session id.
 */
export function getSessionCamData() {
  if (isSessionCamEnabled()) {
    const sessionId = window.sessionCamRecorder.sessionId();

    if (sessionId) {
      return {
        recordingUrl: `https://console.sessioncam.com/Console/Recordings/PlaybackSession?sessionId=${sessionId}`,
      };
    }
  }

  return null;
}

/**
 * Sends a custom parameter (variable) to the SessioncCam configuration object.
 * @see https://help.sessioncam.com/hc/en-gb/articles/360001041714-How-to-set-up-a-Custom-Variable
 */
export function sendCustomParam(key, value) {
  window.sessioncamConfiguration = window.sessioncamConfiguration || {};
  window.sessioncamConfiguration.customDataObjects =
    window.sessioncamConfiguration.customDataObjects || [];
  window.sessioncamConfiguration.customDataObjects.push({ key, value });
}

export function sendCustomDataEvent(key, value) {
  if (isSessionCamEnabled()) {
    window.sessionCamRecorder.sendCustomDataEvent(key, value);
  }
}

/**
 * for example: createVirtualPageLoad('virtual/testpage');
 */
export function createVirtualPageLoad(url) {
  if (!url) {
    console.warn('url is required in create virtual page load');
    return;
  }

  if (typeof url !== 'string') {
    console.warn('url must be a string');
  }

  if (
    isSessionCamEnabled() &&
    window.sessionCamRecorder.createVirtualPageLoad
  ) {
    const trimmedUrl = trim(url);
    window.sessionCamRecorder.createVirtualPageLoad(trimmedUrl);
  }
}

export function closeVirtualPageLoad() {
  if (isSessionCamEnabled() && window.sessionCamRecorder.closeVirtualPage) {
    window.sessionCamRecorder.closeVirtualPage();
  }
}

/**
 * Method that is called after initialize sessionCam
 * @param {Object} app - instance of VA app
 * @param {Object} options - options from va files config
 */
function onInit(app, options = {}) {
  // create virtual page
  if (options.virtualPageUrl) {
    createVirtualPageLoad(options.virtualPageUrl);
  }

  // ✨ add your trackers here
}

/**
 * Сalled to initialize SessionCam
 */
export function initialize(app) {
  // get sessionCam config from app config files
  const [cfg] = app.config.files.filter(
    file => file.name === 'sessionCam' && file.type === 'analytics'
  );

  if (!cfg || !cfg.options) {
    console.error('sessionCam configuration file not found');
    return;
  }

  // If sessionCam missing load manually.
  if (!isSessionCamEnabled()) {
    loadjs(
      'https://d2oh4tlt9mrke9.cloudfront.net/Record/js/sessioncam.recorder.js',
      () => onInit(app, cfg.options)
    );
  } else {
    onInit(app, cfg.options);
  }
}

window.__va__(app => initialize(app));
